/**
  *
  */



  %link {
    color: $link;
    transition: color .2s ease;
    touch-action: manipulation;
    &:hover {
      color: darken($link, 10%);
    }
  }
.qc-cmp-ui h4 {
  font-size: 15px;
  margin: 0 0 10px;
}

%paragraph {
  font-size: 14px;
  margin: 0 0 $spacer;
  color: #333;
}

%h1 {
  font-weight: 700;
  font-size: 25px;
  color: #333;
  margin: 0 0 $spacer;  
}
%h2 {
  font-weight: 700;
  font-size: 18px;
  color: #333;
  margin: 0 0 $spacer;
}
.qc-cmp-ui p {
  @extend %paragraph;
  
}

.qc-cmp-ui-container {
  z-index: 100000000;
  -webkit-font-smoothing: antialiased;
  @extend %font;
  
  p {
    a {
      @extend %link;
    }  
  }
  
}



.qc-cmp-beta-messaging,
.qc-cmp-main-messaging,
.qc-cmp-messaging {
  @extend %paragraph;
}

.qc-cmp-ui {
 /* background: #fff;
  border-radius: 2px;
  box-shadow: 0 1px 3px rgba(33, 41, 52, 0.75);
  flex-direction: column;
  
  max-width: 45em;
  min-width: 10em;
  overflow: hidden;
  position: relative;
  text-align: left;*/
}

.qc-cmp-ui-content {
  padding-top: 0.5em;
  padding-bottom: 0.5em;
  max-width: 45em;
}

/* Buttons */


.qc-cmp-small-toggle.qc-cmp-toggle-on,
.qc-cmp-toggle.qc-cmp-toggle-on {
  background-color: $green !important;
  border-color: $green !important;
}
.qc-cmp-small-toggle,
.qc-cmp-toggle {
  background-color: $gray !important;
  border-color: $gray !important;
}
.qc-cmp-publisher-purposes-invisble-table {
  display: none;
  visibility: hidden;
}
.qc-cmp-toggle-status {
  color: #000 !important;
}
.qc-cmp-toggle-on {
  color: #000 !important;
}
.qc-cmp-title {
  @exstend %h1;
  
}
.qc-cmp-publisher-logo {
  padding-top: 2em;
  padding-bottom: 1em;
}


/* Mobile only */
@media only screen and (max-device-width: 667px) {
  .qc-cmp-sub-title-container {
    flex-direction: column-reverse;
  }

  /* remove padding on mobile */
  .qc-cmp-publisher-logo {
    padding-top: 2em !important;
  }
  .qc-cmp-ui-content {
    padding: 20px 30px !important;
  }
  .qc-cmp-alt-buttons {
    padding-bottom: 2em !important;
  }
}

.qc-cmp-ui-showing {
  height: 100vh;
  overflow: hidden;
}

.qc-cmp-ui-container {
  background: rgba(33, 41, 52, 0.85);
  bottom: 0;
  left: 0;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
  position: fixed;
  right: 0;
  top: 0;
  transition: opacity 0.15s ease;
  z-index: 2147483647;
}
.qc-cmp-ui,
.qc-cmp-ui-container {
  display: flex;
  opacity: 0;
  visibility: hidden;
  will-change: visibility, opacity;
}
.qc-cmp-ui {
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 1px 3px rgba(33, 41, 52, 0.75);
  flex-direction: column;
  max-width: 660px;
  min-width: 320px;
  overflow: hidden;
  position: relative;
}
.qc-cmp-publisher-logo,
.qc-cmp-ui {
  box-sizing: content-box;
  margin: auto;
  width: 100%;
}
.qc-cmp-publisher-logo {
  display: block;
  max-height: 60px;
  max-width: 150px;
  padding-top: 60px;
}
.qc-cmp-ui-content {
  align-self: center;
  display: flex;
  flex-direction: column;
  margin: auto;
  max-width: 600px;
  padding: 30px 30px 0;
}


.qc-cmp-beta-messaging,

.qc-cmp-title {
  @extend %h1;
}





.qc-cmp-beta-messaging {
  text-align: center;
  padding: 35px 0 0;
}




.qc-cmp-buttons {
  align-content: center;
  display: flex;
  flex: 0 1 auto;
  flex-wrap: wrap;
  justify-content: center;
  
}


%icon {
  padding-left: 20px;
  &:before {
    content: '';
  
  background-image: url(data:image/svg+xml;base64,);
  background-size: cover;
  
  display: inline-flex;
  width: 13px;
  height: 13px;
  vertical-align: middle;
  align-items: center;
  justify-content: center;
  opacity: .2;
  margin-right: 4px;
}
  
  

}

%btn {
  @extend %font;
  display: inline-flex;
  height: 40px;
  align-items: center;
  padding: 0 20px;
  border: 0;
  font-weight: 600;
  cursor: pointer;
  justify-content: center;
  transition: all .2s ease;
  border-radius: 2px;
  border: 2px solid transparent;
  white-space: nowrap;
}

%btn--primary {
  background-color: $green;
  color: #fff;
  &:hover {
    background-color: darken($green, 10%);
  }
}
%btn--primary-alt {
  border-color: transparent;
  color: #333;
  background: transparent;
  font-size: 12px;
  padding: 0 10px!important;
  &:hover {
    background-color: transparent;
    color: #000;
    border-color: transparent;
  }
}

%btn--trans {
  color: $gray!important;
  background: transparent;
  font-weight: 600;
  text-transform: none;
  letter-spacing: initial;
  &:hover {
    color: darken($gray, 15%)!important;
  }
}

%btn--footer {
  padding: 0;
  font-weight: 600;
  letter-spacing: initial;
  @extend %btn--primary-alt;
}

%btn--lg {
  font-size: 12px;
  letter-spacing: 1px;
}
%btn--md {
  font-size: 18px;

}
%btn--sm {
  font-size: 12px;

}

.qc-cmp-button {
  @extend %btn;
  @extend %btn--primary;
  @extend %btn--lg;
  box-sizing: border-box;
  margin: 7px;
  
}

.qc-cmp-secondary-button {
  @extend %btn--primary;
}

.qc-cmp-disable-button {
  @extend %btn--primary-alt;

}

// small btn
.qc-cmp-center-bottom {
  @extend %btn;
  @extend %btn--sm;
  @extend %btn--trans;
}


#qc-cmp-purpose-button {
    @extend %icon;
    @extend %btn;
    @extend %btn--footer;
    @extend %btn--sm;
    @extend %btn--trans;
}


.qc-cmp-button-small {
  height: 40px;
  font-size: 12px;
}



.qc-smaller {
  font-size: 12px;
}

.qc-cmp-bottom {
  .qc-cmp-alt-action  {
    @extend %btn;
    @extend %btn--footer;
    
    &:nth-of-type(2) {
    
    
    
    }
  }
}


.qc-cmp-sub-title-container {
  display: flex;
  align-self: center;
  .qc-cmp-buttons {
    flex: 1 0 auto;
    display: flex;
    justify-content: flex-end;
    button {
      margin: 0 7px;
    }
  }
}


.qc-cmp-button-small.qc-cmp-secondary-button,
.qc-cmp-button-small.qc-cmp-secondary-button:hover {
  background: transparent;
  color: #000;
}


.qc-cmp-alt-button {
  border: 0;
  flex-basis: 100%;
  margin-top: 10px;
  outline: none;
  text-decoration: none;
}

.qc-cmp-alt-buttons {
  display: inline-flex;
  justify-content: center;
  margin: 0 auto;
  padding-bottom: 30px;
}

.qc-cmp-initalt-buttons {
  display: flex;
  justify-content: center;
}


.qc-cmp-center-bottom + .qc-cmp-center-bottom {
  border-left: 1px solid #a6a9ae;
}
.qc-cmp-sub-title-container {
  display: flex;
  flex: 0 0 auto;
  border-bottom: 1px solid $border-color;
}
.qc-cmp-sub-title {
  @extend %h1;
}



// toggles

.qc-cmp-toggler {
  display: inline-block;
}
.qc-cmp-toggle-switch {
  position: absolute;
  display: inline-block;
  top: 2px;
  right: 16px;
  width: 19px;
  height: 19px;
  background-color: #fff;
  border-radius: 50%;
  transition: right 0.05s linear;
  box-shadow: 2px 2px 1px rgba(0,0,0,.25);
}
.qc-cmp-toggle {
  background-color: $gray;
  border: 0px solid $gray;
  border-radius: 27px;
  cursor: pointer;
  display: block;
  height: 23px;
  margin: 0 0 0 auto;
  position: relative;
  width: 38px;
  transition: background-color .2s ease, border-color .2s ease;
}

.qc-cmp-small-toggle.qc-cmp-toggle-on, 
.qc-cmp-toggle.qc-cmp-toggle-on {
  background-color: $green;
      border-color: $green;
}

.qc-cmp-toggle-on .qc-cmp-toggle-switch {
    right: 2px;
}



.qc-cmp-bordered {
  border: 1px solid #e8e9ea;
}
.qc-cmp-nav-bar-publisher-logo-container {
  align-items: center;
  display: flex;
  height: 60px;
  margin: 0 0 0 20px;
}
.qc-cmp-nav-bar-publisher-logo {
  display: block;
  max-height: 40px;
  max-width: 150px;
}

.qc-cmp-purposes-page-content {
  height: calc(100vh - 195px);
  max-height: 500px;
  min-height: 250px;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;

  padding: 20px;
}

.qc-cmp-bold-messaging {
  @extend %h2;
}
.qc-cmp-purpose-description {
  margin: 10px 0;
  width: calc(100% - 80px);
  text-align: left;
}
.qc-cmp-purpose-actions {
  align-items: center;
  bottom: 20px;
  display: flex;
  flex-direction: column-reverse;
  justify-content: center;
  position: absolute;
  right: 0;
  top: 20px;
}
.qc-cmp-toggler {
  display: inline-block;
}
.qc-cmp-toggle-status {
  color: #368bd6;
  
  font-size: 12px;
  margin: 10px 0;
  text-align: right;
}
.qc-cmp-show-vendors-link {
  font-size: 12px;
  margin-top: 10px;
  cursor: pointer;
}
.qc-cmp-enabled-cell {
  color: #368bd6;
  
  font-size: 14px;
  margin: 10px 0;
  padding: 0 20px;
  text-align: right;
}
.qc-cmp-purpose-info {
  position: relative;
}
.qc-cmp-table {
  border: 1px solid $border-color;
  border-collapse: collapse;
  font-size: 14px;
  width: 100%;
}

.qc-cmp-table-header {
  color: $gray;
  font-size: 12px;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 1px;
  margin: 0;
  padding: 0 $spacer;
  text-align: left;
  text-transform: uppercase;
}

.qc-cmp-table-row {
  border-top: 1px solid $border-color;
  padding: 10px $spacer;
}

.qc-cmp-publisher-purposes-table {
  margin-bottom: 20px;
}

.qc-cmp-nav-bar {
  display: flex;
  width: 100%;
  justify-content: flex-end;
}

.qc-cmp-nav-bar.qc-cmp-top {
  border-bottom: 1px solid #e8e9ea;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}

.qc-cmp-top-nav-item {
  box-sizing: border-box;
  display: flex;
  flex: 1 0 227px;
  height: 60px;
}

.qc-cmp-nav-bar.qc-cmp-bottom {
  flex-wrap: wrap;
  border-top: 1px solid #e8e9ea;
  align-items: center;
  justify-content: space-between;
  align-content: flex-start;
  flex-direction: row-reverse;
}



.qc-cmp-cancel {
  flex: 0 1 100px;
  line-height: 75px;
}

.qc-cmp-save-and-exit {
  flex: 1 1 200px;
  margin: 10px 20px 10px 0;
  max-width: 165px;
}
.qc-cmp-partner-info {
  margin: 40px 40px 0;
}
.qc-cmp-vendor-list-container {
  border: 1px solid #e8e9ea;
  border-bottom: none;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;

}
.qc-cmp-all-vendors-list {
  height: 240px;
}
.qc-cmp-purposes-vendor-list,
.qc-cmp-vendor-list {
  border-bottom: 1px solid #e8e9ea;
  border-collapse: collapse;
  color: #212934;
  
  font-size: 14px;
  margin: 0;
  width: 100%;
}
.qc-cmp-purposes-vendor-list {
  margin: 20px 0 10px;
}
.qc-cmp-vendor-column-header {
  text-align: left;
  text-transform: uppercase;
}
.qc-cmp-vendor-list-title {
  color: #575f68;
  margin: 0;
  font-size: 11px;
  font-weight: 700;
  line-height: 30px;
  letter-spacing: 2px;
}
.qc-cmp-vendor-row {
  height: 45px;
  border-top: 1px solid #e8e9ea;
}
.qc-cmp-company-column {
  text-align: left;
  padding: 0 15px;
}
.qc-cmp-dropdown-column,
.qc-cmp-on-off-column {
  width: 50px;
}

.qc-cmp-company-cell {
  font-size: 14px;
  font-weight: 700;
  padding: 0 20px;
  text-align: left;
}

.qc-cmp-toggle-cell {
  text-align: center;
}

.qc-cmp-dropdown-cell {
  font-size: 18px;
  text-align: center;
}

.qc-cmp-arrow-down {
  border: solid #333;
  border-width: 0 2px 2px 0;
  cursor: pointer;
  height: 10px;
  margin: auto;
  transform: rotate(45deg) translateX(-5px);
  width: 10px;
}

.qc-cmp-flip-up {
  transform: rotate(225deg);
}

.qc-cmp-vendor-info-content {
  padding: 0 20px 15px;
  font-size: 12px;
  line-height: 16px;
  text-align: left;
}

.qc-cmp-link,
.qc-cmp-vendor-policy {
  font-size: 12px;
}
.qc-cmp-link {
  cursor: pointer;
  
  font-weight: 600;
  text-align: left;
  text-decoration: none;
}
.qc-cmp-link,
.qc-cmp-link:visited {
  color: #368bd6;
}
.qc-cmp-link:hover {
  color: rgba(54, 139, 214, 0.6);
}
.qc-cmp-left-nav-link {
  flex: 0 0  auto;
  line-height: 75px;
  margin: 0 0 0 20px;
  position: relative;
  text-align: left;
  white-space: nowrap;
}
.qc-cmp-back {
  padding-left: 25px;
}
.qc-cmp-back:before {
  content: "";
  display: block;
  position: absolute;
  top: 29px;
  left: 0;
  width: 16px;
  height: 16px;
  /*background: url(data:image/svg+xml;charset=utf-8,<svg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16' fill='none' stroke='%234D545D' stroke-width='2' stroke-linecap='round' stroke-linejoin='round'><path d='M2 5l6 6 6-6'></svg>) 50% no-repeat;*/
  transform: rotate(90deg);
}
.qc-cmp-showing {
  visibility: visible;
  opacity: 1;
}
.qc-cmp-hidden {
  display: none;
}
.qc-cmp-no-height {
  height: 0;
}



@media screen and (max-width: 550px) {
  

  .qc-cmp-nav-bar.qc-cmp-top {
    /* flex-direction: column; */
    align-items: center;
    justify-content: center;
  }
  .qc-cmp-nav-bar-publisher-logo-container {
    margin: 0;
  }
  .qc-cmp-hide-with-small-width {
    display: none;
  }

  .qc-cmp-cancel {
    flex: 1 1 auto;
    line-height: 40px;
    margin-top: 20px;
  }

  .qc-cmp-left-nav-link {
    flex: 1 1 auto;
    line-height: 40px;
    margin: 20px 0 0 0;
    text-align: center;
  }

  .qc-cmp-save-and-exit {
    flex: 1 1 auto;
  }
}

@media screen and (min-width: 550px) {
  .qc-cmp-ui {
    max-height: 650px;
  }
  

}

.qc-cmp-toggle-status {
  display: none;
}

.qc-cmp-alt-action.qc-cmp-cancel {
  order: 1;
}
@media screen and (max-width: 550px) {
  .qc-cmp-partner-info {
    margin: 20px 10px 0;
  }
.qc-cmp-ui.qc-cmp-showing {
    display: grid;
    grid-template: 1fr auto/1fr;
    grid-auto-rows: 140px;
    
    grid-gap: 10px;
    height: 100%;
  }
  .qc-cmp-alt-action.qc-cmp-left-nav-link,
  .qc-cmp-alt-action.qc-cmp-cancel {  
    margin-top: 0!important;

  }
  .qc-cmp-alt-action.qc-cmp-left-nav-link.qc-cmp-back {
    order: -1;
  }
  .qc-cmp-button.qc-cmp-save-and-exit {
    max-width: calc(100% - 50px);
    margin: 0 auto;
    order: 99;
  }
  .qc-cmp-nav-bar.qc-cmp-bottom {
    align-items: flex-start!important;
    line-height: 0;
  }
}